.nav__toggle {
  position: absolute;
  top: -54px;
  right: 0;
  z-index: 2;
  display: block;
  width: 30px;
  height: 34px;
  padding: 0;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav--nojs .nav__list {
    display: block;
    position: static;
  }
}
.nav--opened .nav__toggle::before,
.nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  top: 20px;
  right: 0;
  width: 24.76px;
  height: 1.5px;
  background-color: #000000;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
